import React from 'react'
import style from './css/Item.module.scss'

const Item = ({items, itemOpen, onItemClose}) => {

    return (
        <div className={itemOpen ? style.itemOverlay : style.itemOverlayHidden} 
                onClick={onItemClose}>
            <div className={style.Item} onClick={(e) => e.stopPropagation()}>
                <img alt={style.cart} src={items.image}/>
                <div>
                    <p>{items.title}</p>
                    <b>{items.price}</b>
                </div>                            
            </div>
        </div>
    )
}

export default Item
